import React from 'react';
import bridge from '@vkontakte/vk-bridge'; // VK Brige


import { 
    Panel,
    PanelHeader,
    Group,
    Alert,
    Counter,
    SimpleCell,
    Snackbar,
    PanelHeaderBack,
    CellButton,
    } from '@vkontakte/vkui';

import Icon28DoneOutline from '@vkontakte/icons/dist/28/done_outline';
import Icon28CoinsOutline from '@vkontakte/icons/dist/28/coins_outline';
import Icon28DeleteOutline from '@vkontakte/icons/dist/28/delete_outline';
import Icon28PaletteOutline from '@vkontakte/icons/dist/28/palette_outline';
import Icon20CancelCircleFillRed from '@vkontakte/icons/dist/20/cancel_circle_fill_red';
import Icon28TargetOutline from '@vkontakte/icons/dist/28/target_outline';
import Icon28InfoOutline from '@vkontakte/icons/dist/28/info_outline';


export default class Settings extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            api_url: "https://xelene.ru/road/php/index.php?",

        }
        var propsbi = this.props.this;
        this.setPopout = propsbi.setPopout;
        this.showErrorAlert = propsbi.showErrorAlert;
        this.showAlert = propsbi.showAlert;
        this.setActiveModal = propsbi.setActiveModal;
        this.onChange = (event) => {
            var name = event.currentTarget.name;
            var value = event.currentTarget.value;
            this.setState({ [name]: value });
        }
    }
    deleteAccount(){
      fetch(this.state.api_url + "method=account.delete&" + window.location.search.replace('?', ''))
      .then(res => res.json())
      .then(data => {
      if(data.result) {
        bridge.send("VKWebAppClose", {"status": "success"});
      }else{
          this.showErrorAlert(data.error.message)
      }
      })
      .catch(err => {
          this.showErrorAlert(err)

      })
    }

    render() {
        var props = this.props.this;
        return(
            <Panel id={this.props.id}>
                <PanelHeader 
                    left={
                        <PanelHeaderBack onClick={() => this.props.this.goBack()} /> 
                }>
                    
                </PanelHeader>
                <Group>
                  <SimpleCell
                  disabled
                  indicator={this.props.account.age}
                  before={<Icon28TargetOutline/>}> </SimpleCell>
                  
                </Group>
                <Group>
                  <SimpleCell
                  before={<Icon28PaletteOutline />}
                  expandable
                  onClick={() => props.goPanel('schemechange')}> </SimpleCell>
                  <SimpleCell
                  onClick={() => this.props.this.setSnack(
                    <Snackbar
                    layout="vertical"
                    onClose={() => this.props.this.setSnack(null)}
                    before={<Icon20CancelCircleFillRed width={24} height={24} />}
                  >
                          .   :)
                  </Snackbar>)}
                  before={<Icon28DoneOutline />}></SimpleCell>
                </Group>
                <Group>
                  <SimpleCell
                  disabled
                  indicator={<Counter>{this.props.account.balance}</Counter>}
                  before={<Icon28CoinsOutline />}></SimpleCell>
                  <SimpleCell
                  expandable
                  onClick={() => {
                      this.props.this.goPanel("info");
                  }}
                  before={<Icon28InfoOutline />}> </SimpleCell>
                </Group>
                <Group>
                  <CellButton 
                  mode="danger"
                  onClick={() => this.setPopout(<Alert
                    actionsLayout='vertical'
                    actions={[{
                      title: ',    ',
                      autoclose: true,
                      style: 'cancel'
                    },{
                      title: ' ',
                      autoclose: true,
                      mode: 'destructive',
                      action: () => this.deleteAccount(),
                    }]}
                    onClose={() => this.setPopout(null)}
                  >
                    <h2>, </h2>
                    <p>      :  ,    ,     .     ?</p>
                </Alert>)}
                  before={<Icon28DeleteOutline />}> </CellButton>
                </Group>
                {this.props.this.state.snackbar}
            </Panel>
        )
    }
}
